import Vue from 'vue'
import VueRouter from 'vue-router'
import { Dialog } from 'vant'

// 对常用的几个组件设置懒加载
// 一级路由，其他一级路由在下面
import layout from '@/views/layout'
import editaddress from '@/views/pay/editaddress'

// 二级路由，这部分是layout下的二级路由
import home from '@/views/layout/home'
import category from '@/views/layout/category'
import cart from '@/views/layout/cart'
import user from '@/views/layout/user'

// 导入vuex实例化的store，这样就可以在js文件中使用store了
// 一级路由
import store from '@/store/index'
const search = () => import('@/views/search')
const searchlist = () => import('@/views/search/list')
const prodetail = () => import('@/views/prodetail')
const login = () => import('@/views/login')
const pay = () => import('@/views/pay')
const address = () => import('@/views/pay/address')
const myorder = () => import('@/views/myorder')
Vue.use(VueRouter)
const router = new VueRouter({
  routes: [
    {
      path: '/',
      component: layout,
      redirect: '/home',
      children: [
        {
          path: '/home',
          component: home
        },
        {
          path: '/category',
          component: category
        },
        {
          path: '/cart',
          component: cart
        },
        {
          path: '/user',
          component: user
        }
      ]
    },
    {
      path: '/search',
      component: search
    },
    {
      path: '/searchlist',
      component: searchlist
    },
    {
      // 动态路由传参
      path: '/prodetail/:id',
      component: prodetail
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/pay',
      component: pay
    },
    {
      path: '/address',
      component: address
    },
    {
      path: '/editaddress',
      component: editaddress
    },
    {
      path: '/myorder',
      component: myorder
    }

  ]
})

// 设置路由前置守卫，其实就用来筛查路径，并对相应的路径进行处理
const authUrl = ['/pay', '/myorder', '/address']
router.beforeEach((to, form, next) => {
  // 1. to 往哪里去， 到哪去的路由信息对象
  // 2. from 从哪里来， 从哪来的路由信息对象
  // 3. next() 是否放行
  //  如果next()调用，就是放行
  //  next(路径) 拦截到某个路径页面
  const token = store.getters.token
  // 先判断是不是需要鉴权的路径
  // to和from中都有path和fullpath
  // path不包含查询参数，fullpath包含查询参数
  if (!authUrl.includes(to.path)) {
    next()
    return
  }
  // 在判断本地是否有token，没有则拦截，并转到登录页
  if (!token) {
    // vant组件Dialog，确认框
    Dialog.confirm({
      title: '温馨提示',
      message: '此操作需要先登录',
      confirmButtonText: '去登录',
      cancelButtonText: '再逛逛'
    })
      .then(() => {
        // on confirm
        // 按确认后要做的事
        next('/login')
      })
      .catch(() => {
        // on cancel
        // 按取消后要做的事，这里什么都不做，所以为空
      })
  } else {
    next()
  }
})

export default router
