<template>
  <div class="home">
    <!-- 导航条 -->
    <van-nav-bar title="欢乐购" fixed />

    <!-- 搜索框 -->
    <van-search
      readonly
      shape="round"
      background="#f1f1f2"
      placeholder="请在此输入搜索关键词"
      @click="$router.push('/search')"
    />

    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for=" item in swipeList" :key="item.imgUrl">
        <img :src="item.imgUrl" alt="">
      </van-swipe-item>
    </van-swipe>

    <!-- 导航 -->
    <van-grid column-num="5" icon-size="40">
      <van-grid-item
        v-for="item in navList" :key="item.imgUrl"
        :icon="item.imgUrl"
        :text="item.text"
        @click="$router.push('/category')"
      />
    </van-grid>

    <!-- 主会场 -->
    <div class="main">
      <img :src="bigPic.imgUrl" alt="">
    </div>

    <!-- 猜你喜欢 -->
    <div class="guess">
      <!-- 标签并没有渲染出来，而是当普通文本显示了  -->
      <!-- <p class="guess-title">—— {{guessText}} ——</p>  -->
      <!-- 如果想让标签也渲染出来可以使用v-html -->
      <p class="guess-title" v-html="guessText"></p>
      <div class="goods-list">
        <GoodsItem v-for="item in goodsList" :key="item.goods_id" :item="item"></GoodsItem>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getLayoutDataApi } from '@/api/layout'
export default {
  name: 'HomePage',
  data () {
    return {
      swipeList: {},
      navList: {},
      bigPic: {},
      guessText: '',
      goodsList: []
    }
  },
  created () {
    this.getLayoutData()
  },
  methods: {
    async getLayoutData () {
      const res = await getLayoutDataApi()
      // console.log(res.data.pageData.items)
      this.swipeList = res.data.pageData.items[1].data
      this.bigPic = res.data.pageData.items[3].data[0]
      this.navList = res.data.pageData.items[4].data
      this.guessText = res.data.pageData.items[5].params.content
      this.goodsList = res.data.pageData.items[6].data
    }
  },
  components: {
    GoodsItem
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.home {
  padding-top: 100px;
  padding-bottom: 50px;
}

// 导航条样式定制
.van-nav-bar {
  z-index: 999;
  background-color: #c21401;
  ::v-deep .van-nav-bar__title {
    color: #fff;
  }
}

// 搜索框样式定制
.van-search {
  position: fixed;
  width: 100%;
  top: 46px;
  z-index: 999;
}

// 分类导航部分
.my-swipe .van-swipe-item {
  height: 185px;
  color: #fff;
  font-size: 20px;
  text-align: center;
  background-color: #39a9ed;
}
.my-swipe .van-swipe-item img {
  width: 100%;
  height: 185px;
}

// 主会场
.main img {
  display: block;
  width: 100%;
}

// 猜你喜欢
.guess .guess-title {
  height: 40px;
  line-height: 40px;
  text-align: center;
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
