<template>
  <div class="editaddress">
    <van-nav-bar
      :title="titleText"
      left-text="返回"
      right-text="完成"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    <van-cell-group>
      <van-field
      v-model="name"
      label="姓名"
      placeholder="请输入姓名"
      />
    </van-cell-group>
    <van-cell-group>
      <van-field
      v-model="phone"
      label="电话"
      placeholder="请输入电话号码"
      />
    </van-cell-group>
    <van-field
      v-model="Location"
      is-link
      readonly
      label="地区"
      placeholder="请选择所在地区"
      @click="show = true"
    />
    <van-popup v-model="show" round position="bottom">
      <van-cascader
        v-model="cascaderValue"
        title="请选择所在地区"
        :options="options"
        @close="show = false"
        @finish="onFinish"
      />
    </van-popup>
    <van-cell-group>
      <van-field
      v-model="detailAddress"
      label="详细地址"
      placeholder="请输入详细地址"
      />
    </van-cell-group>
  </div>
</template>

<script>
// EditReceivedDetailApi
import { getReceivedDetailApi } from '@/api/address'
export default {
  name: 'EditAddress',
  data () {
    return {
      titleText: '',
      show: false,
      // 保存合并后的省市区，字符串
      Location: '',
      // 保存合并后的省市区代码，字符串
      LocationCode: '',
      // 将省市区和省市区代码处理后的数据
      region: [],
      // 详细地址
      detailAddress: '',
      // 联级值，每选中一级就会变成这级所对应的值
      cascaderValue: '',
      phone: '',
      name: ''
      // 选项列表，children 代表子选项，支持多级嵌套，
      // 已在vuex中的address中处理好了数据，并通过计算属性options引过来了，可以直接用
      // options: [
      //   {
      //     text: '浙江省',
      //     value: '330000',
      //     children: [{ text: '杭州市', value: '330100', children: [{ text: '杭州市', value: '330100' }] }]
      //   },
      //   {
      //     text: '江苏省',
      //     value: '320000',
      //     children: [{ text: '南京市', value: '320100' }]
      //   }
      // ]
    }
  },
  created () {
    this.$store.dispatch('address/getAddressSource')
    this.EditFn(this.addressId)
    this.titleText = this.addressId ? '编辑地址' : '新增地址'
  },
  methods: {
    // 地区全部选项选择完毕后，会触发 finish 事件
    onFinish ({ selectedOptions }) {
      this.show = false
      this.Location = selectedOptions.map((option) => option.text).join('/')
      this.LocationCode = selectedOptions.map((option) => option.value).join('/')
    },
    onClickLeft () {
      // 点击左边的按钮要做的事
      this.$router.replace('/address')
    },
    onClickRight () {
      this.handleSelAddress()
      // 点击右边的按钮要做的事
      const obj = {
        name: this.name,
        phone: this.phone,
        region: this.region,
        detail: this.detailAddress
      }
      // 没有addressId传过来表明是新增地址模式，需要使用新增地址Api
      // 有addressId传过来表明是编辑模式，需要使用编辑地址Api
      if (!this.addressId) {
        this.$store.dispatch('address/addAddress', obj)
      } else {
        this.$store.dispatch('address/EditAddress', {
          addressId: this.addressId,
          obj
        })
      }
      this.$router.replace('/address')
      // console.log(this.region)
      // console.log(obj)
    },
    // 处理下选好的省市区地址
    handleSelAddress () {
      // 每次处理选好的省市区地址时，由于使用的是push，所以要处理之前输入的脏数据
      this.region = [] // 置空
      for (let key = 0; key < 3; key++) {
        this.region.push({
          value: this.LocationCode.split('/')[key],
          label: this.Location.split('/')[key]
        })
      }
    },
    // 有addressId传过来为编辑模式，数据需要回显
    async EditFn (addressId) {
      if (!addressId) return
      const { data: { detail } } = await getReceivedDetailApi(addressId)
      // console.log(detail)
      this.name = detail.name
      this.phone = detail.phone
      this.Location = detail.region.province + '/' + detail.region.city + '/' + detail.region.region
      this.LocationCode = detail.province_id + '/' + detail.city_id + '/' + detail.region_id
      this.detailAddress = detail.detail
    }
  },
  computed: {
    options () {
      return this.$store.getters['address/nationlAddressList']
    },
    addressId () {
      return this.$route.query.addressId
    }
  }
}
</script>

