<template>
    <div  class="cart">
      <van-nav-bar title="购物车" fixed />
      <div v-if=" cartTotalNum > 0 && token">
        <!-- 购物车开头 -->
        <div class="cart-title">
          <span class="all">共<i>{{cartTotalNum}}</i>件商品</span>
          <span class="edit" @click="toggleEdit">
            <van-icon name="edit"/>
            {{isEdit ? '完成' : '编辑'}}
          </span>
        </div>

        <!-- 购物车列表 -->
        <div class="cart-list">
          <div class="cart-item" v-for="item in cartList" :key="item.id">
            <van-checkbox :value="item.isChecked" @click="handlecheck(item.id)"></van-checkbox>
            <div class="show" @click="$router.push(`/prodetail/${item.goods_id}`)">
              <img :src="item.goods.goods_image" alt="">
            </div>
            <div class="info">
              <span class="tit text-ellipsis-2">{{item.goods.goods_name}}</span>
              <span class="bottom">
                <div class="price">¥ <span>{{item.goods.goods_price_min}}</span></div>
                <div class="count-box">
                <!-- 替换为自定义组件 -->
                  <!-- 由于会修改商品数量，所以这里不能用v-model直接绑定，绑定就变成在组件中修改数据了，而我们需要在vuex中进行修改 -->
                  <!-- 但可以用:value + @input来处理 ，为了能同时接收CountBox组件传到当前组件的数据和其他数据，需要使用 箭头函数 或 $.event-->
                  <!-- <CountBox :value="item.goods_num" @input="handleCount(item.goods.goods_id,item.goods.goods_sku_id)" ></CountBox> 会导致无法接收CountBox传过来的商品数量-->
                  <CountBox :value="item.goods_num"  @input="value=>{handleCount(item.goods_id,item.goods_sku_id,value)}"></CountBox>
                  <!-- 或 -->
                  <!-- <CountBox :value="item.goods_num"  @input="handleCount(item.goods.goods_id,item.goods_sku_id,$event)"></CountBox> -->
              </div>
              </span>
            </div>
          </div>
        </div>

        <div class="footer-fixed">
          <div  class="all-check">
            <van-checkbox  icon-size="18" :value="checkAllState" @click="isAll"></van-checkbox>
            全选
          </div>

          <div class="all-total">
            <div class="price">
              <span>合计：</span>
              <span>¥ <i class="totalPrice">{{selGoodsTotalPrice}}</i></span>
            </div>
            <div v-if="!isEdit" @click="goPay" :class="{ disabled: selGoodsNum == 0}" class="goPay">结算({{selGoodsNum}})</div>
            <div v-else @click="deleteGoods" :class="{ disabled: selGoodsNum == 0}" class="delete">删除({{selGoodsNum}})</div>
          </div>
        </div>
      </div>
      <div class="empty-cart" v-else>
        <img src="@/assets/empty.png" alt="">
        <div class="tips">
          您的购物车是空的, 快去逛逛吧
        </div>
        <div class="btn" @click="$router.push('/')">去逛逛</div>
      </div>
    </div>
</template>

<script>
import CountBox from '@/components/CountBox.vue'
export default {
  name: 'CartPage',
  data () {
    return {
      isEdit: false
    }
  },
  created () {
    // 先判断有没有登录
    if (!this.token) return
    this.$store.dispatch('cart/getCartList')
  },
  methods: {
    // 处理商品选中状态
    handlecheck (id) {
      this.$store.commit('cart/changeSelGood', id)
    },
    // 点击切换全选状态
    isAll () {
      this.$store.commit('cart/isAll', !this.checkAllState)
    },
    // 处理商品数量
    handleCount (goodsId, goodsSkuId, goodsNum) {
      this.$store.dispatch('cart/updateCartGoodsInfo', {
        goodsId,
        goodsSkuId,
        goodsNum
      })
    },
    // 编辑模式控制
    toggleEdit () {
      this.isEdit = !this.isEdit
      // 点击时将所有商品取消勾选状态，再次点击编辑时，全部商品变为勾选状态
      this.$store.commit('cart/isAll', !this.isEdit)
    },
    // 删除操作
    deleteGoods () {
      this.$store.dispatch('cart/deleteCartGoods')
    },
    // 前往支付页
    goPay () {
      const cartIds = this.$store.getters['cart/selGoods'].map(item => item.id).join(',')
      this.$router.push({
        path: '/pay',
        query: {
          // 在购物车下去结算需要传mode和cartIds
          mode: 'cart', // 购物车下位cart
          cartIds // 购物车中对应商品的购物车id字符串，用','分隔
        }
      })
    }
  },
  computed: {
    token () {
      return this.$store.getters.token
    },
    // 购物车列表
    cartList () {
      return this.$store.state.cart.cartList
    },
    // 购物车商品总数量
    cartTotalNum () {
      return this.$store.getters['cart/cartGoodsTotal']
    },
    // 选中商品的数量
    selGoodsNum () {
      return this.$store.getters['cart/selGoodsNum']
    },
    // 选中商品总价格
    selGoodsTotalPrice () {
      return this.$store.getters['cart/selGoodsTotalPrice']
    },
    // 全选的状态确定
    checkAllState () {
      return this.cartList.every(item => item.isChecked === true)
    }
  },
  components: {
    CountBox
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }

}

.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
