<template>
  <div class="category">
    <!-- 分类 -->
    <van-nav-bar title="全部分类" fixed />

    <!-- 搜索框 -->
    <van-search
      readonly
      shape="round"
      background="#f1f1f2"
      placeholder="请输入搜索关键词"
      @click="$router.push('/search')"
    />

    <!-- 分类列表 -->
    <div class="list-box" v-if="categoryData">
      <div class="left">
        <ul>
          <li v-for="(item,index) in categoryData" :key="item.category_id">
            <!-- 利用下标更改激活状态 -->
            <a :class="{ active: index === activeIndex }" @click="activeIndex = index" href="javascript:;">{{item.name}}</a>
          </li>
        </ul>
      </div>
      <div class="right">
        <div  class="cate-goods" v-for="item in categoryChild" :key="item.category_id" @click="goSearchListpage(item.category_id)">
          <img :src="item.image.external_url" alt="">
          <p>{{item.name}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategoryApi } from '@/api/category'
export default {
  name: 'CategoryPage',
  data () {
    return {
      // 主分类
      categoryData: [],
      // 子分类
      categoryChild: [],
      // 激活的导航栏元素的下标
      activeIndex: 0

    }
  },
  created () {
    this.getCategory()
  },
  methods: {
    async getCategory () {
      const res = await getCategoryApi()
      // console.log(res.data.list)
      this.categoryData = res.data.list
      // 默认选第一个分类
      this.categoryChild = res.data.list[0].children
    },
    goSearchListpage (id) {
      this.$router.push(`/searchlist?categoryId=${id}`)
    }
  },
  watch: {
    activeIndex: {
      handler (value) {
        // 当左边导航栏激活的标签变化时触发
        // 由于有的没有children，所以要进行判断
        // const childrenData = this.categoryData[value].children
        // if (childrenData) {
        //   this.categoryChild = childrenData
        // } else {
        //   this.categoryChild = []
        // }

        // 其实还有一种解决方法 使用 ?.
        // item in list[activeIndex]?.children 中问号的作用：在JavaScript中，?.被称为可选链操作符。
        // 例如，如果有一个可能不存在的对象list[activeIndex]，并且你想访问它的children属性，
        // 你可以写list[activeIndex]?.children。如果list[activeIndex]存在，并且有children属性，
        // 那么这个表达式就会返回children的值。如果list[activeIndex]不存在，或者list[activeIndex]没有children属性，
        // 那么这个表达式就会返回undefined，而不会抛出错误。
        // 这样就可以在不确定list[activeIndex]是否存在的情况下，安全地访问list[activeIndex].children
        // 因此上面可以改成：
        this.categoryChild = this.categoryData[value]?.children || [] // 当然，也可以直接写在使用v-for那部分里面
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.category {
  padding-top: 100px;
  padding-bottom: 50px;
  height: 100vh;
  .list-box {
    height: 100%;
    display: flex;
    .left {
      width: 85px;
      height: 100%;
      background-color: #f3f3f3;
      overflow: auto;
      a {
        display: block;
        height: 45px;
        line-height: 45px;
        text-align: center;
        color: #444444;
        font-size: 12px;
        &.active {
          color: #fb442f;
          background-color: #fff;
        }
      }
    }
    .right {
      flex: 1;
      height: 100%;
      background-color: #ffffff;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-content: flex-start;
      padding: 10px 0;
      overflow: auto;

      .cate-goods {
        width: 33.3%;
        margin-bottom: 10px;
        img {
          width: 70px;
          height: 70px;
          display: block;
          margin: 5px auto;
        }
        p {
          text-align: center;
          font-size: 12px;
        }
      }
    }
  }
}

// 导航条样式定制
.van-nav-bar {
  z-index: 999;
}

// 搜索框样式定制
.van-search {
  position: fixed;
  width: 100%;
  top: 46px;
  z-index: 999;
}
</style>
